import { useEffect, useState } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import './App.css';
const App = () => {
  const [text, setText] = useState('')              // Вводим переменную для текста, введенного с клавиатуры
  useEffect(()=>{
    const typing = (e) =>{
      let key = e.key.toUpperCase()                 // Приводим к верхнему регистру для простоты сравнения
      if(e.key === 'Backspace'){                    // Если нажата клавиша backspace,
        setText(text.slice(0,-1))                   // Удаляем последний символ
        return document.body.removeEventListener("keydown", typing) // Отписываемся от события, чтобы не завис браузер
      }
      if(key.length !== 1){                         // Отсеиваем нажатия на клавиши alt, shift, tab, enter и т.д.
        return 
      }
      let isEnglish = ( key >= 'A' && key<= 'Z')    // Проверяем введенный символ на англ. буквы
      let isRussian = ( key >= 'А' && key <= 'Я')   // Проверяем введенный символ на русские буквы
      if( isEnglish ||  isRussian || key === "Ё"){  // Куда же без Ё ?))
        setText(text + key);                        // Добавляем введенный символ в переменную объявленную в строке 5
        return document.body.removeEventListener("keydown", typing) // Отписываемся от события, чтобы не завис браузер
      }
      
    }
    document.body.scrollIntoView(false);                    // Прокрутка до конца body
    document.body.addEventListener("keydown", typing);      // Подписываемся на событие и выполняем функцию
  },[text])                                                 // Зависимость для обновления компонента
  let textArr = text.split('')                              // Разбиваем строку на массив для функции map
  return (
    <div className="App">
      <TransitionGroup>                                     
      {textArr.map((t,index)=>(                             
        <CSSTransition classNames='fade' timeout={{ enter: 3000, exit: 500 }} key={index} >   
          <div className='addedText' >{t}</div> 
        </CSSTransition>
      ))}
    </TransitionGroup>
    <div className='cursor'></div>                          
    <div className='fullText'>                
      Написанное слово : {text}                             
    </div>
    </div>
  );
}

export default App;
